import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_RESIZE_HEIGHT = Symbol('TABLE_RESIZE_HEIGHT');

export function resizeTableHeight(table, rowsHeight, delta: number) {
  return {
    type: TABLE_RESIZE_HEIGHT,
    payload: {
      node: table,
      rowsHeight,
      delta,
    },
  };
}

interface TableResizeTableHeightPayload {
  node: Table;
  delta: number;
  rowsHeight: number[];
}

interface TableResizeTableHeightAction {
  type: typeof TABLE_RESIZE_HEIGHT;
  payload: TableResizeTableHeightPayload;
}

export function isTableResizeTableHeightAction(
  action: Action,
): action is TableResizeTableHeightAction {
  return action.type === TABLE_RESIZE_HEIGHT;
}
